<template>
  <div class="agreement">
    <nav-head title="用户协议" FixedOrTransparency="isFixed NoTransparency"/>
    <div class="padding-25" v-html="content">
      <div>{{content}}</div>
    </div>
  </div>
</template>
<script>//
import NavHead from '@/components/NavHead.vue'
import http from '../../tool/http'
import { Toast } from 'mint-ui'

export default {
  name: 'agreement',
  data: () => {
    return {
      content: ''
    }
  },
  components: {
    NavHead
  },
  created () {
    http.getRegistAgreement().then(data => {
      if (data) {
        this.content = data.post_content
      } else {
        Toast('用户协议获取失败')
      }
    })
  }
}
</script>
<style scoped lang="scss">
  .agreement{
    background-color: white;
    line-height: 24px;
  }
  .padding-25 {
    padding: 25px;
  }
</style>
